const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			imgUrl: '',
			nextPageUrl: ''
		};
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		// 加载查稿列表
		this.myDraftPosts()
	},
	onReachBottom() {
		// 页面上拉触底事件的处理函数
		if (this.nextPageUrl != '') {
			this.getMore()
		}
	},
	methods: {
		goDetails(e) {
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: `/pages/articleDetail?id=${id}`
			})
		},
		myDraftPosts() {
			// 加载查稿列表
			uni.showLoading({
			  title: '列表获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/user/my-draft-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					if (res.data.status_code == 200) {
						this.list = res.data.data
						this.nextPageUrl = res.data.next_page_url
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			})
		},
		getMore() {
			// 加载更多
			uni.showLoading({
			  title: '列表获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/user/my-draft-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					if (res.data.status_code == 200) {
						let list = this.list
						let newList = res.data.data
						this.list = list.concat(newList)
						this.nextPageUrl = res.data.next_page_url
					} else {
						uni.showToast({
							title: res.data.message
						});
						this.nextPageUrl = ''
					}
				}
			})
		}
	}
}
